.comeBack {
    position: absolute;
    left: 100px;
    top: 50%;
    transition: transform .3s;
    background-color: transparent;
    border: none;
    &:hover {
        transform: translateX(-20px);
    }
    a {
        font-size: 21px;
        color: orange;
        font-weight: 600;
        cursor: pointer;
        text-decoration: none;

    }
}