header {
    border-bottom: 5px solid #c31432;
}

.menu__links:hover {
    color: #c31432
}

.header__profile {
    display: flex;
    gap: 20px;
    align-items: center;
    &-img {
        width: 50px;
        height: 50px;
        border-radius: 50%;
        object-fit: cover;
    }
    &-name {
        font-size: 18px;
    }
}